/* my style */
.sh-burger {
  position: relative;
  display: none;
  width: 18px;
  height: 18px;
  margin-right: 18px;
  cursor: pointer;
  transition-duration: .2s; }
  .sh-burger span,
  .sh-burger span::before,
  .sh-burger span::after {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 18px;
    height: 2px;
    margin-top: -1px;
    margin-left: -9px;
    background-color: #757982; }
  .sh-burger span::before,
  .sh-burger span::after {
    content: '';
    transition: 0.2s; }
  .sh-burger span::before {
    transform: translateY(-8px); }
  .sh-burger span::after {
    transform: translateY(8px); }
  .sh-burger--active span {
    height: 0; }
    .sh-burger--active span::before {
      transform: rotate(45deg); }
    .sh-burger--active span::after {
      transform: rotate(-45deg); }

.site-header {
