  .my-search-wrap--active .my-search-btn {
    background-image: url(../img/search_cross.svg); }

.my-search {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  max-width: 300px;
  border-radius: 6px;
  border: 1px solid #DADCE0;
